import React from "react";
import Navigation from "../Components/Navigation";
import { Form, Label, Input, FormGroup, Button } from "reactstrap";

function Signup() {
	return (
		<React.Fragment>
			<Navigation isHomeNav={false} />
			<Form className="p-5">
				<div className="row my-5">
					{/* Personal Details */}
					<fieldset className="col-12 col-lg-4 offset-lg-1 border">
						<legend>Account Information</legend>
						<Form>
							<FormGroup>
								<Label className="text-muted">Name</Label>
								<Input
									name="name"
									type="text"
									// value={name}
									// onChange={handleNameChange}
									placeholder="John"
								/>
							</FormGroup>
							<FormGroup>
								<Label className="text-muted">Surname</Label>
								<Input
									name="surname"
									type="text"
									// value={description}
									// onChange={handleDescriptionChange}
									placeholder="Doe"
								/>
							</FormGroup>
							<FormGroup>
								<Label className="text-muted">Username</Label>
								<Input
									name="username"
									type="text"
									// value={price}
									// onChange={handlePriceChange}
									placeholder="Username"
								/>
							</FormGroup>
							<FormGroup>
								<Label className="text-muted">Password</Label>
								<Input
									name="password"
									type="password"
									// value={quantity}
									// onChange={handleQuantityChange}
									placeholder="***********"
								/>
							</FormGroup>
							<FormGroup>
								<Label className="text-muted">Confirm Password</Label>
								<Input
									name="confirmPassword"
									type="password"
									// value={quantity}
									// onChange={handleQuantityChange}
									placeholder="***********"
								/>
							</FormGroup>
						</Form>
					</fieldset>
					{/* Delivery Details */}
					<fieldset className="col-12 col-lg-4 offset-lg-1 border">
						<legend>Delivery Information</legend>
						<FormGroup>
							<Label className="text-muted">Street address</Label>
							<Input
								name="streetAddress"
								type="text"
								// value={name}
								// onChange={handleNameChange}
								placeholder="# street"
							/>
						</FormGroup>
						<FormGroup>
							<Label className="text-muted">Suburb</Label>
							<Input
								name="suburb"
								type="text"
								// value={description}
								// onChange={handleDescriptionChange}
								placeholder="Suburb"
							/>
						</FormGroup>
						<FormGroup>
							<Label className="text-muted">Town</Label>
							<Input
								name="town"
								type="text"
								// value={price}
								// onChange={handlePriceChange}
								placeholder="Town"
							/>
						</FormGroup>
						<FormGroup>
							<Label className="text-muted">Postal Code / ZIP Code</Label>
							<Input
								name="postCode"
								type="number"
								// value={quantity}
								// onChange={handleQuantityChange}
								placeholder="####"
							/>
						</FormGroup>
					</fieldset>
				</div>
				<Button color="primary" className="mx-5">
					Create
				</Button>
			</Form>
		</React.Fragment>
	);
}

export default Signup;
